import React from 'react';

import imgg from '../images/cot-logo-dark-1.png';

function Footer() {
    return (
        <div className="row">
                    <div className="col-lg-12">
                        <div className="widget">
                            <div className="text-widget">
                                <div className="footer-nav horizontal-menu">
                                    <ul>
                                        <li><a href="#">Home</a></li>
                                        <li><a href="#">Company</a></li>
                                        <li><a href="#">Services</a></li>
                                        <li><a href="#">career</a></li>
                                        <li><a href="#">Insight</a></li>
                                        <li><a href="#">Contact Us</a></li>
                                    </ul>
                                </div>
                                <div className="about-info">
                                    <div className="image-wrapper">
                                        <img src={imgg} alt="" width="180px" height="50px"/>
                                    </div>
                                </div>
                                <div className="newsletter-form style-two">
                                    <h3>Subscribe to our newsletter</h3>
                                    <form method="post">
                                        <div className="form-group">
                                            <input type="email" className="form-control" id="EmailInput"
                                                placeholder="Enter email address" required=""></input>
                                        </div>
                                        <button type="submit">Subscribe Now!</button>
                                    </form>
                                </div>
                                <div className="social-media-links">
                                    <ul>
                                        <li><a target="_blank" href="https://www.facebook.com/sacredthemes/"><i
                                            className="fab fa-facebook-f"></i></a></li>
                                        <li><a target="_blank"
                                            href="https://www.linkedin.com/company/sacredthemes/"><i
                                                className="fab fa-linkedin-in"></i></a></li>
                                        <li><a target="_blank" href="https://twitter.com/SacredThemes"><i
                                            className="fab fa-twitter"></i></a></li>
                                        <li><a target="_blank" href="https://www.instagram.com/sacred_themes/"><i
                                            className="fab fa-instagram"></i></a></li>
                                        <li><a target="_blank"
                                            href="https://www.youtube.com/channel/UCk7Jjmrlry0DiyiXoENs_TA"><i
                                                className="fab fa-youtube"></i></a></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        
    )
}

export default Footer;